#card {
  width: 0;
  height: 0;
  border-top: 20px solid #e8f4f8;
  border-right: 25px solid #c1e1ec;
  border-bottom: 20px solid #d4ebf2;
  border-left: 25px solid #c1e1ec;
  box-shadow: 10px 10px 5px #71858d;;
  position: relative;
}

div.envelop .heart {
  width: 10px;
  height: 10px;
  background-color: #ff1414;
  transform: translate(-50%, -50%) rotate(45deg);
  animation: heartbeat 1.4s infinite;
}

div.envelop .heart:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #ff1414;
  transform: translateY(-50%);
  border-radius: 50%;
}

div.envelop .heart:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #ff1414;
  transform: translateX(-50%);
  border-radius: 50%;
}

@keyframes heartbeat {
  0% {
    transform: translate(-50%, -50%) rotate(45deg) scale(1);
  }

  25% {
    transform: translate(-50%, -50%) rotate(45deg) scale(1);
  }

  30% {
    transform: translate(-50%, -50%) rotate(45deg) scale(1.4);
  }

  50% {
    transform: translate(-50%, -50%) rotate(45deg) scale(1.2);
  }

  70% {
    transform: translate(-50%, -50%) rotate(45deg) scale(1.4);
  }

  100% {
    transform: translate(-50%, -50%) rotate(45deg) scale(1);
  }
}

